import ProjectsPage from "../components/projects/ProjectsPage";
import ToDoPage from "../components/todos/ToDoPage";
import AboutPage from "../components/about/AboutPage";
import NotFoundPage from "../components/core/errors/NotFoundPage";

const baseUrl = process.env.BASE_URL;

const ROUTES = {
    MAIN: { name: "main", path: `${baseUrl}`, redirect: `${baseUrl}projects`, props: true },
    PROJECTS: { name: "projects", path: `${baseUrl}projects`, component: ProjectsPage, props: true },
    PROJECT: { name: "project", path: `${baseUrl}projects/:projectId`, component: ToDoPage, props: true },
    ABOUT: { name: "about", path: `${baseUrl}about`, component: AboutPage, props: true },
    NOT_FOUND: { name: "not-found", path: `${baseUrl}not-found`, component: NotFoundPage, props: true },
}

const ROUTES_LIST = [
    ROUTES.MAIN,
    ROUTES.PROJECTS,
    ROUTES.PROJECT,
    ROUTES.ABOUT,
    ROUTES.NOT_FOUND,
    { path: "*", redirect: ROUTES.NOT_FOUND.path },
];

export default ROUTES;
export { ROUTES_LIST };
